@import url(https://fonts.googleapis.com/css2?family=Nunito&family=Poppins&display=swap);
body {
    margin: 0;
    background-color: black;
    background-image: url(/static/media/background.e688e345.jpg);
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    background-repeat: no-repeat;
    overflow-x: hidden;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}
body::-webkit-scrollbar { /* Chrome, Safari, Opera */
    display: none;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.section-header {
    margin-left: 5vw;
}
.section-header i {
    font-size: 3.6vh;
    font-weight: 900;
    display: flex;
    color: tomato;
}
.section-header b {
    color: dodgerblue;
    font-family: 'Nunito', sans-serif;
    font-weight: 900;
}
.section-line {
    height: 0.1vh;
    background-color: rgb(40, 40, 40);
}

.about {
    margin: auto;
    width: 50%;
}
.about-dp img {
    border-radius: 100%;
    height: 20vh;
    width: 20vh;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
.about-dtl p {
    font-family: 'Nunito', sans-serif;
    font-size: 2.5vh;
    font-weight: 800;
    color: rgb(200, 200, 200);
    text-align: center;
    line-height: 2vh;
}
.dtl-tom {
    color: tomato;
}
.about-dtl span {
    color: dodgerblue;
}
.skills-lang img {
    width: 2vw;
    height: auto;
    margin-left: 1vw;
}
.tab-block {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4vh;
    overflow-x: hidden;
}


/* Long Screen */
@media only screen and (max-width: 600px) {
    .section-header i {
        font-size: 2.5vh;
        font-weight: 900;
        display: flex;
        color: tomato;
    }
    .section-header b {
        color: dodgerblue;
        font-family: 'Nunito', sans-serif;
        font-weight: 900;
    }
    .about-dp img {
        border-radius: 100%;
        height: 18vh;
        width: 18vh;
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
    .skills-lang img {
        width: 6vw;
        height: auto;
        margin-left: 1vw;
    }
    .about-dtl p {
        font-family: 'Nunito', sans-serif;
        font-size: 2vh;
        font-weight: 800;
        color: rgb(200, 200, 200);
        text-align: center;
        line-height: 2vh;
    }
    .tab-block {
        display: contents;
        justify-content: center;
        align-items: center;
        overflow-x: hidden;
    }
}
.Header {
    background-color: black;
}
@-webkit-keyframes nameAnim {
    0% { color: white; }
    50% { color: rgb(175, 175, 175); }
    100% { color: white; }
}
@keyframes nameAnim {
    0% { color: white; }
    50% { color: rgb(175, 175, 175); }
    100% { color: white; }
}
.name {
    color: rgb(200, 200, 200);
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
}
.name i {
    display: flex;
    font-size: 5vw;
    padding: 3vh;
    color: rgb(75, 75, 75);
}
.name b {
    font-size: 4vw;
    font-weight: 900;
    padding: 3vh;
    color: white;
    -webkit-animation-name: nameAnim;
            animation-name: nameAnim;
    -webkit-animation-duration: 60s;
            animation-duration: 60s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    font-family: 'Source Code Pro', monospace;
}

/* Long Screen */
@media only screen and (max-width: 600px) {
    .name i {
        font-size: 12vw;
        padding: 3vh;
        color: rgb(75, 75, 75);
    }
    .name b {
        font-size: 9vw;
        padding: 3vh;
        color: white;
        font-family: 'Source Code Pro', monospace;
    }
}
.Footer a {
    padding: 4vw;
    font-size: 3vh;
}
/* Long Screen */
@media only screen and (max-width: 600px) {
    .Footer a {
        padding: 6vw;
        font-size: 1.8vh;
    }
}

